@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Martel:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@300&display=swap);
html {
  min-height: 100%;
  font-family: "Martel", serif;
}

body {
  margin: 0;
  font-family: "Martel", serif;
}

h1 {
  font-family: "Sansita Swashed", cursive;
}
.card-title {
  font-family: "Merriweather Sans", sans-serif;
}

h2 {
  font-family: "Sansita Swashed", cursive;
}

h3 {
  font-family: "Sansita Swashed", cursive;
}

h4 {
  font-family: "Merriweather Sans", sans-serif;
}

.navbar-collapse {
  font-family: "Merriweather Sans", sans-serif;
}

.dropdown-item {
  font-family: "Merriweather Sans", sans-serif;
}

.card-text {
  font-family: "Merriweather Sans", sans-serif !important;
}

.btn {
  font-family: "Merriweather Sans", sans-serif;
}

.card {
  margin-bottom: 2rem;
}

.btn:focus {
  background-color: #6aae7d !important;
  border: 1px solid#9cc9a8 !important;
}

.newsletter {
  width: 100%;
  height: 380px;
  background-color: #f37472;
  padding-top: 3%;
}

.email-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 2%;
  color: white;
}

.email-col p {
  margin-left: 0;
  color: white;
}

.email-col h2 {
  font-weight: bold;
}

.email-col .form-group {
  width: 100%;
}
.icon {
  display: flex;
  justify-content: center;
  margin: auto;
}

.newsletter-icon path {
  fill: white !important;
}

.footer {
  width: 100%;
  height: 80px;
  margin-top: 2rem;
  padding-left: 5rem;
  padding-right: 5rem;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

p {
  margin: 2rem;
  font-family: "Martel", serif;
  color: black;
}

.logo-footer {
  display: flex;
  align-items: center;
}

.social-logo {
  margin-right: 2rem;
}

.presentation {
  width: 100% !important;
  height: 600px;
  margin-bottom: 0px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(/MDS-EnglishChallengeWeek-Dulatame/static/media/thumbnail.c57b0329.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid #b4b4b1;
}

.txt-presentation {
  width: 70%;
  height: auto;
  padding: 4rem;
  border-radius: 15px;
  background-color: white;
}

.txt-presentation h1 {
  margin-bottom: 2rem;
}

.card-img-top {
  width: 100%;
  height: 15vw;
}

.search-area {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 350px;
  background-color: #9cc9a8;
  color: white;
  border: 1px solid #b4b4b1;
}

form {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input-group {
  margin-top: 2rem;
  width: 50%;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem !important;
  background-color: white;
}

.input-group input {
  margin: auto;
}

.craftsmen-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.craftsmen-list h2 {
  font-size: 50px;
}
.craftsmen-list h4 {
  font-size: medium;
}

.card {
  border: 6px solid #df9453;
  border-radius: 25px;
  height: 470px;
  text-align: center;
}

.card-detail {
  border: none;
  height: 470px;
}

.card-header {
  background-color: white;
  border: none;
  margin: 5px;
}

.img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.img-detail {
  width: 150px;
  height: 150px;
}

.body-detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.h5 {
  font-family: "Sansita Swashed", cursive;
  font-size: 25px;
}

.card-text {
  font-family: "Merriweather Sans", sans-serif;
  margin: 10px;
}

.location {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  margin-bottom: 20px;
}

.location p {
  margin: 0px;
  margin-left: 20px;
}

.fa-map-marker-alt path {
  fill: #f37472;
}

.fa-gifts path {
  fill: #f37472;
}

.img-body {
  width: 50px;
